var _this;
// import ImageCropper from "@/components/invinbg-image-cropper/invinbg-image-cropper.vue";
export default {
	data() {
		return {
			// isLogin: false,
			user:{
				
			}
		}
	},
	mounted() {
		_this = this;
	},
	// components:{
	// 	ImageCropper
	// },
	onLoad:function(){
		
	},
	onShow:function(){
		console.log("我的页面显示")
		var b = this.isLogin();
		if (b){
			// this.isLogin = true;
			this.user = this.getGlobalUser();
			console.log(this.user);
			
			var url = this.getSessionUrl("tb/tbuser/isLogin");
			
			uni.request({
				url: url,
				method: 'GET',
				data: {},
				success: res => {
					if(res.data.code != 0){
						_this.removeGlobalUser();
						uni.navigateTo({
							url:"/pages/login/login?to=me"
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}else{
			uni.navigateTo({
				url:"/pages/login/login?to=me"
			});
		}
	},
	methods: {
		updateEmail(){
			uni.navigateTo({
				url: 'email',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		
		logout(){
			var _this = this;
			var url = this.getSessionUrl("tb/tbuser/logout")
			
			uni.request({
				url: url,
				method: 'GET',
				success: res => {
					console.log(res);
					if (res.data.code == 0){
						_this.removeGlobalUser();
						uni.switchTab({
							url:"/pages/index/index"
						})
					}else{
						uni.showToast({
							icon: 'none',
							position: 'bottom',
							title: "退出登录失败"
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
